import { PaymentAddress, TPayment } from "lib/api";
import { utils } from "near-api-js";
import toast from "react-simple-toasts";
import { useNearContext } from "./near-context";

const basePath = `${window.location.origin}/payment`;

export const useWallet = (payment: TPayment | null): ((currency: PaymentAddress) => void) => {
  const app = useNearContext();

  return async (currency: PaymentAddress) => {
    if (currency.blockchain !== "near") {
      toast("At the moment we only support near blockchain");
      return;
    }

    if (!app?.selector.isSignedIn()) {
      app?.selectorModal.show();
      return;
    }

    const wallet = await app?.selector.wallet();
    if (wallet == null || payment == null) return;

    switch (currency.token) {
      case "NEAR":
        return await wallet.signAndSendTransaction({
          actions: [{ type: "Transfer", params: { deposit: currency.amount } }],
          callbackUrl: `${basePath}/${payment.payment_id}/pending`,
          receiverId: currency.address,
        });

      default:
        const BOATLOAD_OF_GAS = utils.format.parseNearAmount("0.00000000003")!;
        return await wallet.signAndSendTransaction({
          actions: [
            {
              type: "FunctionCall",
              params: {
                methodName: "ft_transfer",
                gas: BOATLOAD_OF_GAS,
                deposit: "1",
                args: {
                  amount: currency.amount,
                  receiver_id: currency.address,
                },
              },
            },
          ],
          callbackUrl: `${basePath}/${payment.payment_id}/pending`,
          receiverId: currency.contract,
        });
    }
  };
};
